import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  Button,
  Container,
  Form,
  FormFeedback,
  FormGroup,
  Input,
  Modal,
  ModalBody,
  ModalHeader,
  Spinner,
} from "reactstrap";
import Base from "../Base";
import {
  clearSuccess,
  sendOtpReducer,
  verifyOtpReducer,
} from "../reducers/authReducer";
import { useDispatch, useSelector } from "react-redux";
import { ErrorAlert, SuccessAlert } from "../shared-components/AlertBox";

const Security = () => {
  const args = {
    backdrop: false,
    centered: true,
    size: "sm",
    fade: false,
  };

  //const regex = `/^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i`;
  const regex = /^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$/;
  const [userValid, setUserValid] = useState(null);
  const [errors, setErrors] = useState({
    email: "",
    mobile: "",
  });

  const { error, loading, success, registeredUser, userdetails } = useSelector(
    (state) => state.user
  );

  const [formData, setFormData] = useState({
    email: registeredUser ? registeredUser : "",
    otp: "",
  });
  const dispatch = useDispatch();

  const inputHandler = (e) => {
    setFormData({ ...formData, [e.target.id]: e.target.value });
  };

  const validateHandler = (e) => {
    if (formData.email === "") {
      setErrors({ ...errors, email: "" });
      setUserValid(null);
    } else if (formData.email !== "" && regex.test(formData.email) === false) {
      setErrors({ ...errors, email: "Email is not valid" });
      setUserValid("invalid");
    } else {
      setErrors({ ...errors, email: "" });
      setUserValid("valid");
    }
  };
  const navigate = useNavigate();

  const token = userdetails ? userdetails.token : null;

  useEffect(() => {
    if (token) {
      dispatch(clearSuccess());
      navigate("/");
    }
    if (success === "OTP verified successfully!! Change your password.") {
      navigate("/change_password");
    }
  }, [token, success]);

  const submitSendOTPHandler = async () => {
    await dispatch(sendOtpReducer(formData));
  };
  const submitResendOTPHandler = async () => {
    await dispatch(sendOtpReducer(formData));
  };
  const submitVerifyOTPHandler = async () => {
    await dispatch(verifyOtpReducer(formData));
  };

  return (
    <>
      <Base isFooter="hideFooter">
        <Container>
          <Modal isOpen={true} style={{ pointerEvents: "none" }} {...args}>
            <ModalHeader>Verify OTP</ModalHeader>
            <ModalBody>
              <Form>
                {!registeredUser ? (
                  <FormGroup>
                    <Input
                      type="text"
                      id="email"
                      placeholder="Enter User Id"
                      valid={userValid && userValid === "valid"}
                      invalid={userValid === "invalid" ? true : false}
                      value={formData.email}
                      onChange={(e) => inputHandler(e)}
                      onBlur={(e) => validateHandler(e)}
                    />
                    <FormFeedback>{errors.email}</FormFeedback>
                  </FormGroup>
                ) : (
                  <>
                    <h6>Otp has been sent to your email id.</h6>

                    <FormGroup>
                      <Input
                        type="text"
                        id="otp"
                        placeholder="Enter OTP"
                        value={formData.otp}
                        onChange={(e) => inputHandler(e)}
                      />
                      <FormFeedback>{errors.mobile}</FormFeedback>
                    </FormGroup>
                  </>
                )}
              </Form>
              <div className="d-grid py-2">
                {!registeredUser ? (
                  <Button
                    className="primary-button"
                    disabled={
                      formData.email === "" ||
                      regex.test(formData.email) === false
                    }
                    onClick={submitSendOTPHandler}
                  >
                    Send OTP
                  </Button>
                ) : (
                  <>
                    <Button
                      className="primary-button"
                      disabled={
                        formData.name === "" ||
                        formData.email === "" ||
                        formData.password === "" ||
                        regex.test(formData.email) === false ||
                        formData.c_password !== formData.password
                      }
                      onClick={submitVerifyOTPHandler}
                    >
                      Verify OTP
                    </Button>
                    <Button
                      outline
                      size="sm"
                      className="my-2 primary-button"
                      onClick={submitResendOTPHandler}
                    >
                      Re-send OTP
                    </Button>
                  </>
                )}
              </div>
            </ModalBody>
            {loading && (
              <span className="position-absolute top-50 start-50 translate-middle">
                <Spinner size="sm" color="primary">
                  Loading...
                </Spinner>
              </span>
            )}
          </Modal>
        </Container>

        {error && <ErrorAlert message={error} />}
        {success && <SuccessAlert message={success} />}
      </Base>
    </>
  );
};

export default Security;
